import React from "react";

import Badge from "../Badge/Badge";
import Card from "../Card/Card";
import { Icon } from "../Icon/Icon";
import { colors, spacingMap } from "../styleguide";
import Typography from "../Typography/Typography";

import {
	IconWrapper,
	PostImage,
	PostImageContainer,
	StyledLink,
} from "./Post.styles";
import { PostProps } from "./Post.types";

const Post = ({
	dataQA,
	ariaLabel,
	padding,
	badgeIcon,
	badgeVariant,
	badgeText,
	header,
	body,
	link,
	firstImage,
	firstImageAltText,
	secondImage,
	secondImageAltText,
	mt,
	mb,
	mr,
	ml,
	m,
	className,
}: PostProps) => (
	<Card
		mt={mt || "1rem"}
		mb={mb}
		mr={mr}
		ml={ml}
		m={m}
		ariaLabel={ariaLabel}
		dataQA={dataQA}
		isClickable
		padding={padding || spacingMap.lg}
		className={className}>
		<Badge
			dataQA={`${dataQA}-badge`}
			icon={badgeIcon || "Info"}
			text={badgeText || "New"}
			variant={badgeVariant || "blue"}
		/>
		<Typography
			mt="2rem"
			mb="1rem"
			component="h2"
			dataQA={`${dataQA}-header`}
			variant="headlineSmall"
		>
			{header}
		</Typography>
		<Typography
			mb="1rem"
			component="p"
			dataQA={`${dataQA}-body`}
			variant="bodyCopy"
		>
			{body}
		</Typography>
		<StyledLink data-qa="post-link">
			{link}
			<IconWrapper data-qa={`${dataQA}-right-icon`}>
				<Icon name="ArrowRight" size={16} color={colors.midBlue} />
			</IconWrapper>
		</StyledLink>
		<PostImageContainer>
			<PostImage src={firstImage} alt={firstImageAltText}/>
			<PostImage src={secondImage} alt={secondImageAltText}/>
		</PostImageContainer>
	</Card>
);

Post.displayName = "Post";
export default Post;
